* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: #eeeded;
  padding: 0 1rem;
}

.header {
  background-color: #f5ba13;
  margin: auto -1rem;
  padding: 1rem 2rem;
  box-shadow: 0 0 1.5625rem 0 rgba(0, 0, 0, 0.3);
}
.header img {
  width: 18.75rem;
  height: 6.25rem;
}
.main-card {
  width:49%;
  margin: 1.875rem auto 3.125rem auto;
  background: #fff;
  padding: 0.9375rem;
  border-radius: 0.4375rem;
  box-shadow: 1.875rem 1.875rem 1.875rem rgb(138, 137, 137);
}
.main-card h1 {
  margin-bottom:1rem;
  color: #f5ba13;
  font-size: 2.8rem;
  font-family: "Piedra", cursive;
  text-shadow: 0.25rem 0.125rem rgb(138, 137, 137);
}
.main-card .button {
  background: #f5ba13;
  color: #fff;
  border: none;
  box-shadow: 0.1875rem 0.3125rem 0.3125rem 0.3125rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  outline: none;
  font-family: "Piedra", cursive;
  padding: 0.4375rem;
  font-size: 1.6rem;
  text-shadow: 0.25rem 0.125rem rgb(138, 137, 137);
  margin: 1.25rem;
}
.main-card .button:hover{
  background-color: #f0a500;
}
.main-card img{
  min-width: 100%;
}
.main-card .reason-block{
  margin-top: 2rem;
  font-size: 2rem;
  border: solid 0.125rem;
  color:#f5ba13;
}
.other-options button {
  position: absolute;
  right:  0.3125rem;
  bottom: -0.625rem;
  background: #f5ba13;
  color: #fff;
  border: none;
  box-shadow: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  outline: none;
  font-size: 1.2rem;
  transform: rotate(-5deg);
}
.other-options button:hover{
  background-color: #f0a500;
  font-size: 1.5rem;
  transform: rotate(3.8deg);
}

.other-options {
  position: relative;
  background-position: center;
  background: #fff;
  border-radius: 0.4375rem;
  box-shadow: 0 0.3125rem 0.3125rem #ccc;
  padding: 0.625rem;
  width: 30%;
  float: left;
  margin:0.5625rem;
  font-family: 'Acme', sans-serif;
  color: #f0a500;
}
.other-options:hover{
  transform: rotate(0.5deg);
  box-shadow: 0 0.3125rem 0.3125rem #ccc;
}
.other-options h4 {
  font-size: 1.4rem;
  margin-bottom: 0.375rem;
}
.dropdown {
  margin-top: 1.5rem;
  position: relative;
  color: #333;
  cursor: default;
  display: -moz-inline-grid;
}

.dropdown .arrow {
  border-color: #999 transparent transparent;
  border-style: solid;
  border-width: 0.313rem 0.313rem 0;
  content: " ";
  display: block;
  height: 0;
  margin-top: 0.3rem;
  position: absolute;
  right: 0.625rem;
  top: 0.875rem;
  width: 0;
}

.dropdown .arrow.open {
  border-color: transparent transparent #f0a500;
  border-width: 0 0.313rem 0.313rem;
}

.dropdown .selected-value input {
  line-height: 1.5;
  font-size: 1rem;
  background-color: #fff;
  border: 0.063rem solid #ccc;
  border-radius: 0.125rem;
  box-sizing: border-box;
  cursor: default;
  outline: none;
  padding: 0.5rem 3.25rem 0.5rem 0.625rem;
  transition: all 200ms ease;
  width: 100%;
}

.dropdown .options {
  display: none;
  background-color: #fff;
  border: 0.063rem solid #ccc;
  box-shadow: 0 0.063rem 0 rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  margin-top: -0.063rem;
  max-height: 12.5rem;
  overflow-y: auto;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
}

.dropdown .options.open {
  display: block;
}

.dropdown .option {
  box-sizing: border-box;
  color: rgba(51, 51, 51, 0.8);
  cursor: pointer;
  display: block;
  padding: 0.5rem 0.625rem;
}

.dropdown .option.selected,
.dropdown .option:hover {
  background-color:#ffc93c;
  color: #333;
}
footer {
  text-align: center;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

footer p {
  color: #ccc;
}
